<template>
    <div style="overflow:hidden;padding-left:1px;height:45px;margin-top:-30px;">
         <p class="right"><span style="margin-right:14px;font-size:14px;">{{time[$store.state.alllang]}}</span>
            <el-date-picker
            v-model="value1"
            type="date"
            placeholder="" style="font-size:16px;margin-right:10px;">
            </el-date-picker>
            
            <span style="margin-left:30px;margin-right:14px;font-size:14px;" v-if="showtype">{{tyarr[$store.state.alllang]}}</span>
            <el-select v-model="type"   class="top_right"  v-if="showtype">
                <el-option :key="index" :label="item[$store.state.alllang]" :value="index" v-for="(item,index) in typearr1" v-if="$store.state.moneyty == 0">{{item[$store.state.alllang]}}</el-option>
                <el-option :key="index" :label="item[$store.state.alllang]"  :value="index" v-for="(item,index) in typearr2" v-if="$store.state.moneyty == 1">{{item[$store.state.alllang]}}</el-option>
                <el-option :key="index" :label="item[$store.state.alllang]"  :value="index" v-for="(item,index) in typearr3" v-if="$store.state.moneyty == 2">{{item[$store.state.alllang]}}</el-option>
                <el-option :key="index" :label="item[$store.state.alllang]"  :value="index" v-for="(item,index) in typearr4" v-if="$store.state.moneyty == 3">{{item[$store.state.alllang]}}</el-option>
                <el-option :key="index" :label="item[$store.state.alllang]"  :value="index" v-for="(item,index) in typearr5" v-if="$store.state.moneyty == 4">{{item[$store.state.alllang]}}</el-option>
                <el-option :key="index" :label="item[$store.state.alllang]"  :value="index" v-for="(item,index) in typearr6" v-if="$store.state.moneyty == 5">{{item[$store.state.alllang]}}</el-option>
                <el-option :key="index" :label="item[$store.state.alllang]"  :value="index" v-for="(item,index) in typearr7" v-if="$store.state.moneyty == 6">{{item[$store.state.alllang]}}</el-option>
            </el-select>
         </p>
    </div>
</template>

<script>
export default {
    data(){
        return{
            // typearr1:[['','Total'],['','Exchanges'],['','Games'],['','High-Risk'],['','Marketplaces'],['','Gambling'],['','Other']],
            // typearr2:[['','Total'],['','Game'],['','Tool'],['','Exchange'],['','Marketplaces'],['','Gambling'],['','High-Risk'],['','Other']],
            // typearr3:[['','Total'],['','Game'],['','Tool'],['','Marketplace'],['','Other']],
            // typearr4:[['','Total'],['','Gambling'],['','Game'],['','Other']],
            // typearr5:[['','Other']],
            // typearr6:[['','Other']],
            // typearr7:[['','Other']],
            typearr1:[['','Total'],['','Game'],['','Tool'],['','Exchange'],['','Marketplaces'],['','Gambling'],['','High-Risk'],['','Other']],
            typearr2:[['','Total'],['','Gambling'],['','Game'],['','Other']],
            typearr3:[['','Total'],['','Exchanges'],['','Games'],['','High-Risk'],['','Marketplaces'],['','Gambling'],['','Other']],
            typearr4:[['','Total'],['','Game'],['','Tool'],['','Marketplace'],['','Other']],
            typearr5:[['','Other']],
            typearr6:[['','Other']],
            typearr7:[['','Other']],
            value1: '',
            time:['','Date'],
            tyarr:['','Categories'],
            type:'',
            //
            mnysel:0,
            //
            showtype:true,
            
        }
    },
    created(){
        this.mnysel = this.$store.state.moneyty
        if(this.$store.state.requesttime){
            this.value1 = new Date(this.$store.state.requesttime);
        }else{
             var now = new Date();
            //,
            //250500
            now.setHours(0, 0, 0, 0);
            console.log(now.getTime());
            var tentime = now.getTime()
            var myDate = new Date();
            console.log(myDate.getHours());
            if(myDate.getHours()<=9){
                tentime = tentime - 86400000
            }
      console.log(this.$store.state.moneyty, this.$store.state.requesttime);
            this.$store.commit('gettime',tentime-86400000)
            this.value1 = new Date(this.$store.state.requesttime);
        }
       
        if(this.$route.path == '/chain'){
            this.showtype = false
        }
        this.$store.commit('getdapptype',0)
        this.type = this.$store.state.dapptype
    },
    mounted(){
        
    },
    computed:{
        thetype_money(){
            return this.$store.state.moneyty
        }
    },
    watch:{
        thetype_money(){
            this.type = 0
        },
        value1(n,o){
            console.log(n)
            var date = new Date(n);
            // 
            var time1 = date.getTime();
            //
            this.$store.commit('gettime',time1)
            console.log(time1)
        },
        type(n,o){
            console.log(n)
            this.$store.commit('getdapptype',n)
        }
    },
    methods:{
        changemny(aa){
            this.mnysel = aa
            this.$store.commit('getdapptype',0)
            this.$store.commit('savepage',1)
            this.type = this.$store.state.dapptype
            this.$store.commit('changemoneyty',aa)
        }
    }
}
</script>

<style scoped>
select {
/*ChromeFirefox*/
border: solid 1px #ccc;
/*select*/
appearance:none;
-moz-appearance:none;
-webkit-appearance:none;
/**/
background:#fff;
/*padding*/
padding-right: 14px;
outline: none;
}
/*ie*/
select::-ms-expand { display: none; }
.myselect{
    width: 200px;
    height: 40px;
    border-radius: 5px;
    padding-left: 20px;
    border: none;
    box-sizing: border-box;
    font-size: 14px;
    color: #4f5f6e;
    border-radius: 20px;
    background-color: #e8ecf2;
    /* background-color: #fff; */
}
select option{
    background-color: #fff;
}
select option:hover{
    background-color: #f8f9fa;
}
.mytype{
    float: left;
    width: 120px;
    height: 40px;
    font-size: 14px;
    line-height: 40px;
    box-sizing: border-box;
    margin-right: 20px;
    position: relative;
    border: solid 1px #e0e7ed;
	border-radius: 20px;
    color: #797b8e; 
}
.right{
    float:right;
    color: #797b8e;
    font-weight: 400;
}
</style>

<style>
.right .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width: 194px;
}
.right .el-input--suffix .el-input__inner{
    border-radius: 20px;
    background-color: #f7f8fa;
    width: 200px;
}
.right .el-input__suffix{ 
    right:25px;
}
.right .el-input__inner{
    font-size:14px;
    color:#797b8e;
    height: 30px;
    line-height: 30px;
}
.right .el-input__icon{
    line-height: 30px;
}
</style>



